@import url(https://fonts.googleapis.com/css2?family=KoHo:ital,wght@0,400;1,300&display=swap);
* {
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  font-family: "KoHo", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  display: -webkit-flex;
  display: flex; /* flex: 0.5; */
  -webkit-flex-direction: column;
          flex-direction: column;
  background-color: #7fffd4;
  width: 100vw;
  height: 100vh;
  -webkit-flex: 1 1;
          flex: 1 1;
}

.header-container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex: 0.2 1;
          flex: 0.2 1;
  width: 40%;
}
.header-container > h1 {
  font-size: 75px;
}
.header-container > .input-container {
  display: -webkit-flex;
  display: flex;
  width: 100%;
  -webkit-justify-content: center;
          justify-content: center;
}
.header-container > .input-container > input {
  width: 50%;
  margin: 0 5px;
  padding: 0 5px;
  border-radius: 10px;
  font-family: "KoHo", sans-serif;
  font-weight: bold;
  background-color: rgba(255, 255, 255, 0.3);
  height: 30px;
  transition: background-color 0.5s ease;
}
.header-container > .input-container > input:hover {
  background-color: rgba(255, 255, 255, 0.5);
}
.header-container > .input-container > input:focus {
  outline: none;
  background-color: rgba(255, 255, 255, 0.5);
}
.header-container > .input-container > .add-button {
  width: 10%;
  border-radius: 10px;
  border: none;
  height: auto;
  cursor: pointer;
  padding: 3px;
  box-shadow: rgba(25, 25, 25, 0.04) 0 0 1px 0, rgba(0, 0, 0, 0.1) 0 3px 4px 0;
  font-family: "KoHo", sans-serif;
  font-weight: 300;
  background-color: rgba(255, 255, 255, 0.35);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  transition: all 0.2s ease-in-out;
}
.header-container > .input-container > .add-button:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}
.header-container > .input-container > .error-button {
  width: 10%;
  border-radius: 10px;
  border: none;
  height: auto;
  cursor: pointer;
  padding: 3px;
  box-shadow: rgba(25, 25, 25, 0.04) 0 0 1px 0, rgba(0, 0, 0, 0.1) 0 3px 4px 0;
  font-family: "KoHo", sans-serif;
  font-weight: 300;
  background-color: rgba(235, 15, 15, 0.884);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  transition: all 0.2s ease-in-out;
  -webkit-animation: shake 0.5s;
          animation: shake 0.5s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}
@-webkit-keyframes shake {
  0% {
    -webkit-transform: translate(1px, 1px) rotate(0deg);
            transform: translate(1px, 1px) rotate(0deg);
  }
  10% {
    -webkit-transform: translate(-1px, -2px) rotate(-1deg);
            transform: translate(-1px, -2px) rotate(-1deg);
  }
  20% {
    -webkit-transform: translate(-3px, 0px) rotate(1deg);
            transform: translate(-3px, 0px) rotate(1deg);
  }
  30% {
    -webkit-transform: translate(3px, 2px) rotate(0deg);
            transform: translate(3px, 2px) rotate(0deg);
  }
  40% {
    -webkit-transform: translate(1px, -1px) rotate(1deg);
            transform: translate(1px, -1px) rotate(1deg);
  }
  50% {
    -webkit-transform: translate(-1px, 2px) rotate(-1deg);
            transform: translate(-1px, 2px) rotate(-1deg);
  }
  60% {
    -webkit-transform: translate(-3px, 1px) rotate(0deg);
            transform: translate(-3px, 1px) rotate(0deg);
  }
  70% {
    -webkit-transform: translate(3px, 1px) rotate(-1deg);
            transform: translate(3px, 1px) rotate(-1deg);
  }
  80% {
    -webkit-transform: translate(-1px, -1px) rotate(1deg);
            transform: translate(-1px, -1px) rotate(1deg);
  }
  90% {
    -webkit-transform: translate(1px, 2px) rotate(0deg);
            transform: translate(1px, 2px) rotate(0deg);
  }
  100% {
    -webkit-transform: translate(1px, -2px) rotate(-1deg);
            transform: translate(1px, -2px) rotate(-1deg);
  }
}
@keyframes shake {
  0% {
    -webkit-transform: translate(1px, 1px) rotate(0deg);
            transform: translate(1px, 1px) rotate(0deg);
  }
  10% {
    -webkit-transform: translate(-1px, -2px) rotate(-1deg);
            transform: translate(-1px, -2px) rotate(-1deg);
  }
  20% {
    -webkit-transform: translate(-3px, 0px) rotate(1deg);
            transform: translate(-3px, 0px) rotate(1deg);
  }
  30% {
    -webkit-transform: translate(3px, 2px) rotate(0deg);
            transform: translate(3px, 2px) rotate(0deg);
  }
  40% {
    -webkit-transform: translate(1px, -1px) rotate(1deg);
            transform: translate(1px, -1px) rotate(1deg);
  }
  50% {
    -webkit-transform: translate(-1px, 2px) rotate(-1deg);
            transform: translate(-1px, 2px) rotate(-1deg);
  }
  60% {
    -webkit-transform: translate(-3px, 1px) rotate(0deg);
            transform: translate(-3px, 1px) rotate(0deg);
  }
  70% {
    -webkit-transform: translate(3px, 1px) rotate(-1deg);
            transform: translate(3px, 1px) rotate(-1deg);
  }
  80% {
    -webkit-transform: translate(-1px, -1px) rotate(1deg);
            transform: translate(-1px, -1px) rotate(1deg);
  }
  90% {
    -webkit-transform: translate(1px, 2px) rotate(0deg);
            transform: translate(1px, 2px) rotate(0deg);
  }
  100% {
    -webkit-transform: translate(1px, -2px) rotate(-1deg);
            transform: translate(1px, -2px) rotate(-1deg);
  }
}

.todo-container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
  margin: 10px;
  border-bottom: 1px solid rgb(53, 51, 51);
  padding: 10px 0;
}
.todo-container:last-child {
  border-bottom: none;
  padding-bottom: 0;
}
.todo-container > .checkbox {
  width: auto;
  -webkit-align-self: auto;
          align-self: auto;
}
.todo-container > .text {
  list-style: none;
  width: 90%;
  border: none;
  margin: 0 5px;
  padding: 0 5px;
  border-radius: 10px;
  font-family: "KoHo", sans-serif;
  font-weight: bold;
  background-color: transparent;
  height: 30px;
}
.todo-container > .text:focus {
  outline: 0 0 1px 0 solid black;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
}
.todo-container > button {
  width: 10%;
  border-radius: 10px;
  border: none;
  height: auto;
  cursor: pointer;
  padding: 3px;
  box-shadow: rgba(25, 25, 25, 0.04) 0 0 1px 0, rgba(0, 0, 0, 0.1) 0 3px 4px 0;
  font-family: "KoHo", sans-serif;
  font-weight: 300;
  background-color: rgba(255, 255, 255, 0.15);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  transition: all 0.2s ease-in-out;
}
.todo-container > button:hover {
  -webkit-transform: scale(1.05);
          transform: scale(1.05);
}

.todo-list-container {
  margin: 10px;
  width: 40%;
  /* border: 1px solid black; */
  padding: 0 10px;
  border-radius: 20px;
  background-color: rgba(255, 255, 255, 0.25);
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
  -webkit-flex: 0.8 1;
          flex: 0.8 1;
  max-height: 560px;
  overflow-y: auto;
}
.todo-list-container::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: rgba(255, 255, 255, 0.25);
  -webkit-backdrop-filter: blur(20px);
          backdrop-filter: blur(20px);
  border-radius: 0 50px 50px 0;
}

.todo-list-container::-webkit-scrollbar {
  width: 10px;
}

.todo-list-container::-webkit-scrollbar-thumb {
  background-color: #757272;
  /* border: 2px solid #555555; */
  border-radius: 0 50px 50px 0;
}
.todo-list-container::-webkit-scrollbar-thumb:hover {
  background-color: #5a5959;
}
.todo-list-container > ul {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
.todo-list-container > .todo-list-empty-container {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 100%;
  height: 100%;
  color: #757272;
}
.todo-list-container > .todo-list-empty-container > .bell-icon {
  font-size: 80px;
  margin: 10px;
  text-align: center;
}
.todo-list-container > .todo-list-empty-container > h1 {
  margin: 10px 10px 20px 10px;
  text-align: center;
}
.todo-list-container > .todo-list-empty-container > h2 {
  text-align: center;
}

